.comment_add {
    float: left;
    width: 550px;
    height: 350px;
    display: flex;
    flex-direction: column;
}

.comment_add textarea {
    height: 200px;
    resize: none;
    font-size: 20px;
    line-height: 2em;
}

.comment_add button {
    width: 100px;
}

.flex_box {
    display: flex;
    justify-content: space-between;
}

.flex_box span {
    width: 100%;
}

.flex_box button {
    width: 10%;
}
.comment_show {
    float: right;
    width: 50%;
    display: flex;
    flex-direction: column;
}
